import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { OpenProgramSetupComponent } from './open-program-setup.component';

const routes: Routes = [
  { path: '', component: OpenProgramSetupComponent, canActivate: [AuthGuard] }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OpenProgramSetupRoutingModule { }
